@import "variables";
@import "mixin";
@import "deliveryPopup";
@import "fixed_nav";
@import "authorization";

body {
  font-size: 20px;
  line-height: 22px;
  color: #474747;
  .header {
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: ". headline headline" "logo search cabinet" "navPanel navPanel navPanel";
      grid-template-columns: 25% 1fr 25%;
      .header__headline {
        grid-area: headline;
        p {
          font: 1em/2em @sansregular;
          color: #474747;
          text-align: center;
          background-color: #ececec;
          border-bottom-right-radius: 15px;
          border-bottom-left-radius: 15px;
        }
      }
      .header__logo {
        grid-area: logo;
        img {
          width: 70%;
        }
      }
      .header__mainSearch {
        grid-area: search;
        display: flex;
        flex-wrap: wrap;
        .search {
          width: 100%;
          margin-top: 70px;
          input {
            width: 90%;
            height: 45px;
            padding-left: 25px;
            font: 1.125em/45px @sansregular;
            color: #9e9e9e;
            border: 3px solid @mainColor;
            border-radius: 50px;
            outline: none;
          }
          i {
            font-size: 1.5em;
            position: relative;
            left: -50px;
            top: 2px;
            color: @mainColor;
          }
        }
        .numbers {
          margin-top: 50px;
          ul {
            display: flex;
            li {
              padding: 0 10px;
              i {
                display: flex;
                justify-content: center;
                align-items: center;
                width: 65px;
                height: 65px;
                font-size: 2em;
                color: @mainColor;
                border: 3px solid @mainColor;
                border-radius: 50px;
              }
              a {
                font: 1.6em/2em @sansregular;
                color: #474747;
                text-decoration: none;
              }
            }
          }
        }
        .workTime {
          margin: 55px 0 0 60px;
          display: flex;
          i {
            font-size: 1.5em;
            line-height: 1.725em;
            color: @mainColor;
          }
          ul {
            margin-left: 20px;
            li {
              span {
                font: .7em/1em @sansregular;
              }
            }
          }
        }
      }
      .header__cabinet {
        grid-area: cabinet;
        margin-top: 70px;
        ul {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li {
            a {
              display: flex;
              justify-content: space-around;
              align-content: center;
              text-decoration: none;
              span {
                display: inline-block;
                font: 1.125em/1.1em @sansregular;
                color: #474747;
                text-align: center;
                i {
                  padding: 0 20px;
                  font-size: 2em;
                  color: @mainColor;
                }
                &:hover {
                  text-decoration: underline;
                  cursor: pointer;
                }
              }
            }
            &:nth-child(1) {
              width: 45%;
            }
            &:nth-child(2) {
              width: 45%;
            }
            &:nth-child(3) {
              margin-top: 50px;
              padding: 10px;
              width: 310px;
              height: 50px;
              justify-content: space-between;
              align-items: center;
              border: 1px solid @mainColor;
              border-radius: 5px;
              span {
                i {
                  margin: 0 0 0 15px;
                  padding: 3px;
                  color: #fff;
                  background-color: #7d3daf;
                  border-radius: 5px;
                }

              }
            }
          }
        }
      }
      .header__nav-panel {
        grid-area: navPanel;
        &__major {
          ul {
            display: flex;
            justify-content: space-between;
            margin-top: 50px;
            li {
              width: 16.67%;
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @sansbold;
                color: #fff;
                text-align: center;
                text-decoration: none;
                background-color: #176975;
                border-right: 1px solid #77c9cc;
                border-left: 1px solid #77c9cc;
                &:hover {
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
        &__goods {
          ul {
            display: flex;
            justify-content: space-between;
            li {
              width: 25%;
              .dropMenu {
                width: 1150px;
                position: absolute;
                z-index: 10;
                padding: 0 25px 25px 0;
                display: flex;
                flex-wrap: wrap;
                background-color: @hoverColor;
                li {
                  margin: 20px 0 0 25px;
                  width: 30%;
                  display: flex;
                  flex-direction: column;
                  a {
                    color: #fff;
                    text-align: left;
                    border: none;
                    background-color: @hoverColor;
                    &:hover {
                      text-decoration: underline;
                    }
                  }
                  &:nth-child(1) {
                    font: 1em/1.125em @sansbold;
                    i {
                      line-height: 1.125em;
                    }
                  }
                  ul {
                    display: flex;
                    flex-direction: column;
                    li {
                      margin: 0 0 0 25px;
                      width: 100%;
                      a {
                        font: 1em/1.125em @sansregular;
                      }
                    }
                  }
                }
              }
              a {
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @sansregular;
                color: @linkColor;
                text-align: center;
                text-decoration: none;
                background-color: #77c9cc;
                border: 1px solid @mainColor;
                &:hover {
                  color: #fff;
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
      }
    }
  }
  .content {
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container {
      .containerChild();
      display: grid;
      grid-template-areas: "aside breadcrumb breadcrumb"
                           "aside catalog popProd"
                           "order order order";
      grid-template-columns: 25% 1fr 15%;
      .content__aside {
        grid-area: aside;
        width: 90%;
        div{
          background-color: @fontColor;
          a{
            padding-left: 15px;
          }
          h5{
            padding-left: 15px;
            height: 60px;
            font: 1.125em/60px @sansbold;
            color: #fff;
            &:hover{
              cursor: pointer;
            }
          }
          ul{
            height: 300px;
            overflow: auto;
            overflow-x: hidden;
            background-color: #77c9cc;
            li{
              padding: 5px 25px;
              a{
                padding-left: 0;
                font: 1em/1.125em @sansregular;
                color: @linkColor;
                text-decoration: none;
                &:hover{
                  text-decoration: underline;
                }
              }
              &:nth-child(3n+1){
                a{
                  font-family: @sansbold;
                }
              }
            }
          }
          .active{
            background-color: @linkColor;
          }
          &:first-child{
            border-top-left-radius: 10px;
            border-top-right-radius: 10px;
          }
          &:last-child{
            border-bottom-left-radius: 10px;
            border-bottom-right-radius: 10px;
          }
        }
        .sale{
          background-color: @hoverColor;

        }
        .sale,
        .catalog{
          a{
            font: 1.125em/60px @sansbold;
            color: #fff;
            text-decoration: none;
          }
        }
      }
      .content__catalog__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__catalog {
        grid-area: catalog;
        &__medicalDevices {
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          div {
            h6 {
              margin: 25px 0;
              font: 1.125em/1.25em @sansregular;
              color: #252525;
              i {
                color: @fontColor;
              }
              &:hover {
                cursor: pointer;
              }
            }
            ul {
              li {
                margin-left: 50px;
              }
            }
          }
        }
        &__medicalDevices__info {
          padding-top: 115px;
          h5 {
            font: 2.25em/2.375em @serifitalic;
            color: @linkColor;
          }
          p {
            margin: 20px 0;
            padding: 0 150px 0 0;
            font: 1.125em/1.25em @sansregular;
            color: #252525;
          }
        }
      }
      .content__popularProducts {
        grid-area: popProd;
        padding-top: 150px;
        a {
          display: block;
          font: 1em/1.125em @sansbold;
          color: @linkColor;
          text-align: center;
          text-decoration: none;
        }
        ul {
          li {
            width: 250px;
            height: 350px;
            margin: 25px 0;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            img {
              margin-top: 15px;
              width: 200px;
              height: 200px;
            }
            p {
              padding: 10px;
              font: 1em/1.125em @sansregular;
              color: @linkColor;
              text-align: left;
            }
            .price {
              font: 1.25em/1.375em @sansbold;
              span {
                font: .875em/1em @sansregular
              }
            }
            &:hover {
              border-color: #176975;
              cursor: pointer;
            }
          }
        }
      }
      .content__howToMakeOrder {
        grid-area: order;
        margin: 60px 0;
        display: flex;
        justify-content: space-between;
        div {
          width: 32.5%;
          height: 235px;
          display: flex;
          flex-wrap: wrap;
          background-color: #e6e6e6;
          border-radius: 5px;
          .icons {
            width: 40%;
            display: flex;
            justify-content: flex-end;
            i {
              font-size: 2em;
              color: #1b6b77;
            }
            .arrow {
              margin-top: 30px;
              width: 20%;
              text-align: right;
            }
            .cart,
            .box,
            .truck {
              margin-top: 40px;
              width: 100px;
              height: 100px;
              display: flex;
              justify-content: center;
              align-items: center;
              border: 2px solid #1b6b77;
              border-radius: 75px;
            }
          }
          .learnMore {
            width: 60%;
            display: flex;
            justify-content: center;
            align-items: center;
            a {
              width: 165px;
              height: 45px;
              font: .875em/45px @sansregular;
              color: #bababa;
              text-align: center;
              text-decoration: none;
              border: 1px solid #bababa;
              border-radius: 5px;
              &:hover {
                color: #fff;
                background-color: #1b6b77;
              }
            }
          }
          .info {
            margin-left: 110px;
            width: 100%;
            font: 1em/1.125em @sansregular;
            color: #474747;
            span:nth-child(1) {
              font: 1.125em/1.25em @sansbold;
            }
          }
        }
      }
    }
  }
  .contentAddres {
    display: flex;
    iframe {
      width: 60%;
      height: 600px;
    }
    .contentAddres__contactForm {
      width: 40%;
      display: flex;
      flex-wrap: wrap;
      background-color: #176975;
      .comentary {
        margin: 45px 0 0 45px;
        input {
          padding-left: 15px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
        }
        input[type*="text"] {
          margin: 0 5px 10px;
          width: 245px;
          height: 45px;
        }
        textarea {
          margin: 0 5px 10px;
          padding-left: 15px;
          width: 515px;
          height: 100px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
          resize: none;
        }
        button {
          margin-top: 20px;
          width: 165px;
          height: 45px;
          font: 1.125em/40px @sansregular;
          color: #fff;
          text-align: center;
          background: transparent;
          border: 1px solid #fff;
          border-radius: 10px;
          outline: none;
          &:hover {
            cursor: pointer;
          }
        }
      }
      .contacts {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        p {
          font: 1.125em/1.25em @sansregular;
          color: #fff;
          a {
            color: #fff;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      .social {
        margin-left: 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        ul {
          display: flex;
          justify-content: space-between;
          a {
            margin: 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            font-size: 1.5em;
            color: #176975;
            text-decoration: none;
            background-color: #fff;
            border-radius: 75px;
          }
        }
        p {
          color: #fff;
          text-align: right;
          font: 1em/1.125em @sansbold;
          span:nth-child(1) {
            font: .875em/1em @sansregular;
          }
        }
      }
    }
  }
  ::-webkit-scrollbar{
    width: 10px;
  }
  ::-webkit-scrollbar-button{
    width: 10px;
    height: 0;
  }
  ::-webkit-scrollbar-thumb{
    width: 5px;
    border: 1px solid @linkColor;
    border-radius: 5px;
    background-color: @fontColor;
    box-shadow: 0 1px 1px #fff inset;
  }
  ::-webkit-scrollbar-track{
    width: 5px;
    background-color: @mainColor;
  }
  ::-webkit-scrollbar-track-piece{
    box-shadow: 0 1px 1px #000 inset;
  }
}